@import "../../gobalStyle.scss";
.intro {
  display: flex;

  @include mobile {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .left {
    flex: 2;
    overflow-y: hidden;
    display: flex;
    align-items: center;

    .wrapper {
      width: 500px;
      height: 500px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      float: right;
      padding: 10px;
      overflow: hidden;
      @include mobile {
        background-color: #fff;
      }
      .imageController {
        width: 100%;
        height: 1000px;
        overflow: hidden;
        margin-bottom: 10%;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
          object-fit: cover;
          width: 100%;
          border-radius: 10px;
          @include mobile {
            width: 80%;
            border-radius: 50%;
          }
        }
      }
    }
  }
  .right {
    flex: 2;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
    .wrapper {
      padding: 20px;
      h3 {
        .ityped-cursor {
          animation: blink 0.3s infinite;
          animation-direction: alternate;
        }
        @keyframes blink {
          50% {
            opacity: 1;
          }
          100% {
            opacity: 0;
          }
        }
      }
    }
  }
}
