@import "../../gobalStyle.scss";
.portfolio-swiper{
 
}
.item{
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #eee;
    padding: 5px;
    padding-left: 20px;
    border-radius: 10px;
    @include mobile{
        flex-direction: column;
    }
    .left{
        flex: 2;
        display: flex;
        align-items: flex-start;
        justify-content: flex-start;
        flex-direction: column;
        flex-wrap: wrap;
        padding: 5px;
        @include mobile{
            width: 100%;
            text-align: start;
        }
    }
    .right{
        flex :5;
        @include mobile{
            width: 100%;
            // height: 20px;
        }
        img{
            width: 100%;
        }
    }
}