@import "../../gobalStyle.scss";

.navbar {
  background-color: white;
  color: $mainColor;
  height: 70px !important;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 2;
  overflow-y: hidden;
  transition: 2s all ease;
  z-index: 10;
  .wrapper {
    padding: 0 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    .left {
      display: flex;
      justify-content: space-between;

      .logo {
        font-size: 30px;
        font-weight: 400;
        text-shadow: 0px 0px 10px rgb(155, 149, 149);
        margin-right: 10px;
      }
      .itemContainer {
        display: flex;
        align-items: center;
        gap: 2px;
        padding-right: 10px;
      }
    }
    .right {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: inherit;
      cursor: pointer;
      height: 100%;
      .container {
        height: 100%;
        display: flex;
        align-items: center;
        .menuBAr {
          width: 32px;
          height: 35px;
          display: flex;
          flex-direction: column;
          justify-content: space-around;

          span {
            transition: 0.5s all ease;
            width: 100%;
            height: 3px;
            background-color: $mainColor;
            transform-origin: left;
            padding: 0;
          }
        }
      }
    }
  }
  &.active {
    background-color: $mainColor;
    color: lightgray;
    .container {
      .menuBAr {
        span {
          &:first-child {
            transform: rotate(45deg);
            background-color: white;
          }
          &:nth-child(2) {
            opacity: 0;
          }
          &:last-child {
            background-color: white;
            transform: rotate(-45deg);
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 420px) {
  .navbar {
    .wrapper {
      overflow-y: visible;
      height: 100px;
      align-items: start;
      .left {
        flex-direction: column;
        gap: 1px;
      }
      .right {
        .container {
          align-items: start;
        }
      }
    }
  }
}
