@import "../../gobalStyle.scss";
.contact {
  .TaostMessage {
    z-index: 1000;
    position: fixed;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #49af41;
    color: white;
    width: fit-content;
    height: 50px;
    display: flex;
    align-items: center;
    gap: 100px;

    padding: 20px;
    border-radius: 10px;
    box-sizing: border-box;
    overflow: hidden;
    opacity: 0;
    transition: 0.5s ease opacity;
    &.show {
      opacity: 1;
    }
    .icon {
      cursor: pointer;
      vertical-align: middle;
    }
    .text {
      font-size: 14px;
    }
  }

  h1 {
    font-size: 50px;
    font-weight: 500;
  }
  .ff {
    display: flex;
    align-items: center;
    justify-content: center;
    form {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      gap: 10px;
      width: 50%;
      @include mobile {
        width: 90%;
      }
      .inputGroup {
        display: flex;
        align-items: flex-start;
        justify-content: center;
        flex-direction: column;
        width: 100%;
        label {
          font-weight: 500;
        }
        input,
        textarea {
          border: none;
          background-color: lightgray;
          padding: 10px;
          border-radius: 10px;
          outline: none;
          box-sizing: border-box;
          width: 100%;
          &:hover {
            background-color: inherit;
            border: 1px solid black;
          }
          &:focus {
            background-color: inherit;
            border: 1px solid black;
          }
        }
      }
      .submit {
        border: none;
        outline: none;
        color: white;
        background: rgb(6, 36, 36);
        padding: 5px 50px;
        font-size: 20px;
        font-weight: 300;
        border-radius: 10px;
        cursor: pointer;
        &:hover {
          background-color: white;
          color: rgb(6, 36, 36);
          border: 1px solid rgb(6, 36, 36);
          font-weight: 500;
        }
      }
    }
  }
}
